html {
  --active-link-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  background: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_link:hover {
  color: var(--active-link-color);
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.logo {
  font-size: 0;
}

.is-active {
  color: var(--active-link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

